/* Similar to Team component CSS */
.profil {
    width: 100%;
}

.profil .container {
    width: 100%;
    margin: auto;
    padding: 4rem 0rem;
}

.avatar-image {
    width: 100px; /* Set the width of the avatar image */
    height: 100px; /* Set the height of the avatar image */
    object-fit: cover; /* Maintain aspect ratio and cover the container */
    border-radius: 50%; /* Make the image round */
    margin-top: 10px; /* Adjust the margin as needed */
  }
  

.profil h2 {
    font-size: 3rem;
    text-align: center;
    color: #2E4D44;
}

.profil .container .content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    margin: 4rem 0;
}

.profil .card {
    border: 1px solid #eee;
    border-radius: 8px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
    padding: 1rem;
    text-align: center;
}

.profil img {
    border-radius: 50%;
    margin-top: -2rem;
    max-width: 100px;
    max-height: 100px;
}

.profil .card p {
    color: #787878;
    font-size: 1.1rem;
    margin: 12px;
}

.profil .card span {
    color: var(--primary-color);
    font-size: 1rem;
    font-weight: 700;
}

@media screen and (max-width: 940px) {
    .profil .container .content {
        grid-template-columns: 1fr;
    }

    .profil .card {
        max-width: 100%;
        margin: 1rem;
    }
}

