.user1,
.user2,
.user3,
.user4,
.user5,
.user6 {
  max-width: 130px;
  min-width: 120px;
  max-height: 150px; /* Need minimum height so it doesnt go under, TODO*/
}

.image-container {
    width: 150px;
    height: 120px;
    overflow: hidden; /* This will hide any portion of the image that overflows the container */
  }

  .image-container img {
    width: 100%; /* Make the image take the full width of the container */
    height: 100%; /* Make the image take the full height of the container */
    object-fit: cover; /* This will crop the image to fit within the container while maintaining its aspect ratio */
  }

.testimonials {
    width: 100%;
}

.testimonials .container {
    width: 1240px;
    margin: auto;
    padding: 4rem 0rem;
}

.testimonials h2 {
    font-size: 3rem;
    text-align: center;
    color: #2E4D44;
}

.testimonials .container .content {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 20px;
    margin: 4rem 0;
    margin-bottom: -5px;
}

.testimonials .card {
    border: 1px solid #eee;
    border-radius: 8px;
    box-shadow: 0 3px 10px rgb(0,0,0,.2);
    padding: 1rem;
}

.testimonials img {
    border-radius: 50px;
    margin-top: -2rem;
}


.testimonials .card p {
    color: #787878;
    font-size: 1.1rem;
    margin: 12px;
}

.testimonials .card span {
    color: var(--primary-color);
    font-size: 1rem;
    font-weight: 700;
}

@media screen and (max-width: 940px) {
    .testimonials .container {
        width: 100%;
    }

    .testimonials .container .content {
        grid-template-columns: 1fr;
    }

    .testimonials .card {
        max-width: 100%;
        margin: 1rem;
    }
}
