.approveVid {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
    filter: blur(8px); /* Apply a blur effect */
    mix-blend-mode: overlay; /* Blend the video with the background color */
    background-color: #2E4D44; /* Set the background color to #2E4D44 */
  }
  
  .parent-container {
    padding-top: 1px; /* Add a small padding-top to prevent margin collapse */
  }
  
  .background {
    height: 50vh;
    width: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
    background-color: #2E4D44; /* Set the background color to #2E4D44 */
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    margin-bottom: 70px; /* Increase the margin-bottom value */
  }
  
  .background img {
    background-color: white; /* Set the background color of the image to white */
    margin-bottom: 20px; /* Add margin-bottom to create space below the image */
  }
  
  .background h2,
  .background h1 {
    color: white;
    font-size: 20px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
    margin-bottom: 10px; /* Add margin-bottom to create space below the text */
  }
  
  .background button {
    margin-top: 20px; /* Increase the margin-top value to move the button down */
  }
  